import React, { Fragment, useState } from "react"
import { graphql, navigate } from "gatsby"

const IndexPage = ({data}) => {
  // 필요한 정보만 가져오지만 다시 추출하는 과정에서 길어질 수는 있다. 이를 해결하는 방법은?
  const [posts, setPosts] = useState(data.allMarkdownRemark.edges)
  console.log(posts)
  return <Fragment>
    {/* <div>{data.allMarkdownRemark.edges[0].node.id}</div> */}
    {/* 타이틀에 해당하는 부분 */}
    <h1>JNBMS</h1>
    <div>모바일 개발 (flutter, android, react-native) 기술 블로그</div>
    <div>junebeomlee.dev@gmail.com</div>
    <br/>
    {/* 포스터의 양식 */}
    {posts.map((post) =>{ 
      const {node: { id, frontmatter : {date, slug, title, description, hashtags}}} = post
      return (
        <div key={id} onClick={() => navigate(slug)}>
          <blockquote>{date}</blockquote>
          <h4>{title}</h4>
          <div>{description}</div>
          <div>
            {hashtags.map((hastag, idx) =>  <code key={idx} style={{marginRight: 12}}>{hastag}</code>)}
          </div>
        </div>)
    })}
  </Fragment>
}

export default IndexPage

export const Head = () => <title>JNBMS</title>

export const pageQuery = graphql`
query {
  allMarkdownRemark {
    edges {
      node {
        id
        frontmatter {
          slug
          date
          title
          description
          hashtags
        }
      }
    }
  }
}
`